import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'

import { rhythm } from '../utils/typography'
import PostTags from '../components/postTags'
import { getSessionPassword } from '../utils/passwordProtectUtils.js'

const PostDiv = styled.div`
  margin: 0 auto;
  border-bottom: 1px solid #ebebeb;
  margin-bottom: calc(1.5rem - 1px);
  :first-child {
    margin-top: 2%;
  }
  width: 99%;
  @media (min-width: 767px) {
    width: 90%
  }
`

const PostTitle = styled.h1`
  margin-bottom: ${rhythm(1 / 4)};
  margin-top: ${rhythm(1 / 8)};
`

const PostDate = styled.span`
  color: #8e8e8e;
  font-size: 15px;
`

const PostLink = styled(Link)`
  text-decoration: none;
  color: inherit;
  font-style: italic;
  &:hover {
    transition: opacity .2s ease;
    opacity: .8;
    -webkit-text-decoration-color: inherit;
    text-decoration-color: inherit;
    cursor: pointer;
  }
`

export default ({ data, tagPageName }) => {
  // Only use this password check bool for cosmetic uses, not secure.
  const pw = getSessionPassword();
  const isSessionPasswordSet = pw && pw !== '' ? true : false;
  return (
    <>
      {data.filter(({node}) => {
        // When not on the cv page, filter out those posts
        if ( tagPageName === 'cv' ) return node;
        if ( ! node.frontmatter.tags.includes('cv') ) return node;
        return null;
      }).map(({ node }) => {
        const isProtected = node.fields.encrypted;
        return (
          <PostDiv key={node.id}>
            <PostDate>{node.frontmatter.date}</PostDate>{' '}
            <PostTags tags={node.frontmatter.tags} />
            <PostLink to={node.fields.slug}>
              <PostTitle>{`${isProtected && ! isSessionPasswordSet ? 'Protected: ' : ''}${node.frontmatter.title}`}</PostTitle>
            </PostLink>
            <p>{node.excerpt}</p>
          </PostDiv>
        )
      })}
    </>
  )
}
